.container {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
    border-radius: 10px;
  }
  
  .none {
    display: none;
  }
  
  .beforePlay {
    width: 100%;
    height: auto;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 0;
    z-index: 9999;
  }
  
  .title {
    height: 100%;
    width: 75%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #f6f6f6;
    font-weight: 600;
    font-size: 38px;
    line-height: 30px;
    user-select: none;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  
  .playIcon {
    width: 70px;
    height: 70px;
    background: center/cover url("../../../../../../../assets/icons/play_button_video.svg");
    cursor: pointer;
    margin-left: 20px;
    margin-right: 20px;
  }
  